
<template>
  <div class="cart">
    <h4 style="font-size: 18px;font-weight: bold;margin-bottom: 15px;color: #e3dbdb">全部商品</h4>
    <div class="cart-main">
      <div class="cart-th">
        <div class="cart-th1">全部</div>
        <div class="cart-th2">商品</div>
        <div class="cart-th3">单价（元）</div>
        <div class="cart-th4">数量</div>
        <div class="cart-th5">小计（元）</div>
        <div class="cart-th6">操作</div>
      </div>
      <div class="cart-body">
        <ul
            class="cart-list"
            v-for="(cart, index) in cartInfoList"
            :key="cart.id"
        >
          <li class="cart-list-con1">
            <input
                type="checkbox"
                name="chk_list"
                :checked="cart.isChecked == 1"
                @change="updateChecked(cart, $event)"
            />
          </li>
          <li class="cart-list-con2">
            <img :src="cart.imgUrl"/>
            <div class="item-msg">{{ cart.skuName }}</div>
          </li>
          <li class="cart-list-con4">
            <span class="price">{{ cart.skuPrice }}.00</span>
          </li>
          <!-- 产品数量 -->
          <li class="cart-list-con5">
            <a
                href="javascript:void(0)"
                class="mins"
                @click="handler('minus', -1, cart)">-</a>
            <input
                autocomplete="off"
                type="text"
                minnum="1"
                class="itxt"
                :value="cart.skuNum"
                @change="handler('change', $event.target.value * 1, cart)"
            />
            <a
                href="javascript:void(0)"
                class="plus"
                @click="handler('add', 1, cart)">+</a>
          </li>
          <li class="cart-list-con6">
            <span class="sum">{{ cart.skuNum * cart.skuPrice }}</span>
          </li>
          <li class="cart-list-con7">
            <a class="sindelet" @click="deleteCartById(cart)" style="cursor: pointer">删除</a>
            <br/>
            <a href="#none" style="cursor: pointer">移到收藏</a>
          </li>
        </ul>
      </div>
    </div>
    <div style="min-height: 40px"></div>
    <div class="cart-tool">
      <div class="select-all">
        <input
            class="chooseAll"
            type="checkbox"
            :checked="isAllCheck&&cartInfoList.length>0"
            @change="updateAllCartChecked"
        />
        <span>全选</span>
      </div>
      <div class="option">
        <a @click="deleteAllCheckedCart">删除选中的商品</a>
        <a href="#none">移到我的关注</a>
        <a href="#none">清除下柜商品</a>
      </div>
      <div class="money-box">
        <div class="chosed">已选择 <span>{{ totalNum }}</span>件商品</div>
        <div class="sumprice">
          <em>总价（不含运费） ：</em>
          <i class="summoney">{{ totalPrice }}</i>
        </div>
        <div class="sumbtn">
          <router-link class="sum-btn" to="/trade">结算</router-link>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {mapGetters} from "vuex";
import throttle from "lodash/throttle";

export default {
  name: "ShopCart",
  mounted() {
    this.getData();
  },
  methods: {
    //获取个人购物车数据
    getData() {
      this.$store.dispatch('getCartList');
    },
    //修改某一个产品的个数[节流]
    handler: throttle(async function (type, disNum, cart) {
      switch (type) {
          //加号
        case "add":
          disNum = 1;
          break;
        case "minus":
          disNum = cart.skuNum > 1 ? -1 : 0;
          break;
        case "change":
          // //用户输入进来的最终量，如果非法的（带有汉字|出现负数），带给服务器数字零
          if (isNaN(disNum) || disNum < 1) {
            disNum = 0;
          } else {
            //属于正常情况（小数：取证），带给服务器变化的量 用户输入进来的 - 产品的起始个数
            disNum = parseInt(disNum) - cart.skuNum;
          }
          break;
      }
      //派发action
      try {
        //代表的是修改成功
        await this.$store.dispatch("addOrUpdateShopCart", {
          skuId: cart.skuId,
          skuNum: disNum,
        });
        //再一次获取服务器最新的数据进行展示
        this.getData();
      } catch (error) {
      }
    }, 500),
    //删除某一个产品的操作
    deleteCartById: throttle(async function (cart) {
      try {
        //如果删除成功再次发请求获取新的数据进行展示
        await this.$store.dispatch("deleteCartListBySkuId", cart.skuId);
        this.getData();
      } catch (error) {
        alert(error.message);
      }
    }, 20),
    //修改某个产品的勾选状态
    async updateChecked(cart, event) {
      //带给服务器的参数isChecked，不是布尔值，应该是0|1
      try {
        //如果修改数据成功，再次获取服务器数据（购物车）
        let isChecked = event.target.checked ? "1" : "0";
        await this.$store.dispatch("updateCheckedById", {
          skuId: cart.skuId,
          isChecked,
        });
        this.getData();
      } catch (error) {
        //如果失败提示
        alert(error.message);
      }
    },
    //删除全部选中的产品
    async deleteAllCheckedCart() {
      try {
        //派发一个action
        await this.$store.dispatch("deleteAllCheckedCart");
        //再发请求获取购物车列表
        this.getData();
      } catch (error) {
        alert(error.message);
      }
    },
    //修改全部产品的选中状态
    async updateAllCartChecked(event) {
      try {
        let isChecked = event.target.checked ? "1" : "0";
        //派发action
        await this.$store.dispatch("updateAllCartIsChecked", isChecked);
        this.getData();
      } catch (error) {
        alert(error.message);
      }
    },
  }
  ,
  computed: {
    ...
        mapGetters(["cartList"]),
    //购物车数据
    cartInfoList() {
      return this.cartList.cartInfoList || [];
    }
    ,
    //计算购买产品的总价
    totalPrice() {
      let sum = 0;
      this.cartInfoList.forEach((item) => {
        sum += item.skuNum * item.skuPrice;
      });
      return sum;
    }
    ,
    //用于计算商品数量
    totalNum() {
      let sum = 0;
      this.cartInfoList.forEach((item) => {
        sum += item.skuNum
      });
      return sum;
    }
    ,
    //判断底部复选框是否勾选【全部产品都选中，采勾选】
    isAllCheck() {
      //遍历数组里面原理，只要全部元素isChecked属性都为1===>真 true
      //只要有一个不是1======>假false
      return this.cartInfoList.every((item) => item.isChecked == 1);
    }
    ,
  }
  ,
}
;
</script>

<style lang="less" scoped>
.cart {
  width: 1200px;
  margin: 0 auto;

  h4 {
    margin: 9px 0;
    font-size: 14px;
    line-height: 21px;
  }

  .cart-main {
    .cart-th {
      background: #e3dbdb;
      border: 1px solid #e3dbdb;
      padding: 10px;
      overflow: hidden;

      & > div {
        float: left;
      }

      .cart-th1 {
        width: 25%;
        font-size: 15px;
        font-weight: bold;

        input {
          vertical-align: middle;
        }

        span {
          vertical-align: middle;
        }
      }

      .cart-th2 {
        width: 25%;
        font-size: 15px;
        font-weight: bold;
      }

      .cart-th3,
      .cart-th4,
      .cart-th5,
      .cart-th6 {
        width: 12.5%;
        font-size: 13px;
        font-weight: bold;
      }
    }

    .cart-body {
      margin: 15px 0;
      border: 1px solid #2d2a2a;
      //min-height: 200px;

      .cart-list {
        padding: 10px;
        border-bottom: 1px solid #2d2929;
        overflow: hidden;

        & > li {
          float: left;
        }

        .cart-list-con1 {
          width: 15%;
        }

        .cart-list-con2 {
          width: 35%;

          img {
            width: 100px;
            height: 90px;
            float: left;
          }

          .item-msg {
            float: left;
            width: 200px;
            margin: 0 12px;
            line-height: 18px;
            font-size: 12px;
            color: black;
          }
        }

        .cart-list-con4 {
          width: 10%;
          font-size: 15px;
          font-weight: bold;
          margin-top: 25px;
        }

        .cart-list-con5 {
          width: 15%;
          margin-top: 20px;

          .mins {
            border: 1px solid #706969;
            border-right: 0;
            float: left;
            color: #ddd;
            width: 7px;
            text-align: center;
            padding: 9px;
          }

          input {
            border: 1px solid #706969;
            background-color: #817979;
            width: 40px;
            height: 33px;
            float: left;
            text-align: center;
            font-size: 14px;
            color: #f3eded;
          }

          .plus {
            border: 1px solid #706969;
            border-left: 0;
            float: left;
            color: #ddd;
            width: 7px;
            text-align: center;
            padding: 9px;
          }
        }

        .cart-list-con6 {
          width: 12%;
          margin-top: 25px;
          font-weight: bold;

          .sum {
            font-size: 16px;
          }
        }

        .cart-list-con7 {
          width: 13%;
          margin-top: 23px;
          

          a {
            color: #e3dbdb;
            padding-left: 5px;
            line-height: 25px;
            font-size: 13px;
            transition: all 1s;
          }
          a:hover {
            font-size: 14px;
            color: #4c9bfc;
          }
        }
      }
    }
  }

  .cart-tool {
    overflow: hidden;
    border: 1px solid #2d2929;
    margin-bottom: 200px;

    .select-all {
      padding: 10px;
      overflow: hidden;
      float: left;

      span {
        vertical-align: middle;
      }

      input {
        vertical-align: middle;
      }
    }

    .option {
      padding: 10px;
      overflow: hidden;
      float: left;

      a {
        float: left;
        padding: 3px 10px;
        font-size: 13px;
        color: #e3dbdb;
        cursor: pointer;
        transition: all 1s;
      }
      a:hover {
        color:#4c9bfc;
      }
    }

    .money-box {
      float: right;

      .chosed {
        line-height: 26px;
        float: left;
        padding: 0 10px;
        color: #e3dbdb;
      }

      .sumprice {
        width: 200px;
        line-height: 22px;
        float: left;
        padding: 0 10px;
        font-size: 13px;
        font-weight: bold;

        .summoney {
          color: #c81623;
          font-size: 18px;
          font-weight: bold;
        }
      }

      .sumbtn {
        float: right;

        a {
          display: block;
          position: relative;
          width: 96px;
          height: 52px;
          line-height: 52px;
          color: #fff;
          text-align: center;
          font-size: 18px;
          font-family: "Microsoft YaHei";
          background: #32322e;
          overflow: hidden;
        }
      }
    }
  }
}
</style>
