<script>
  import ImageList from './ImageList/ImageList'
  import Zoom from './Zoom/Zoom'
  import { mapGetters } from 'vuex'

  export default {
    name: 'Detail',
    data(){
      //购买产品个数
      return {
        skuNum:1
      }
    },
    
    components: {
      ImageList,
      Zoom
    },
    mounted() {
      //派发cation获取产品详情的信息
      this.$store.dispatch('getGoodInfo',this.$route.params.skuid)
    },
    computed:{
      ...mapGetters(['categoryView','skuInfo','spuSaleAttrList']),
      //给子组件的数据
      skuImageList(){
        return this.skuInfo.skuImageList||[];
      }
    },
    methods:{
      //产品的售卖属性值切换高亮
      changeActive(saleAttrValue, arr) {
        //遍历全部售卖属性值isChecked为零没有高亮了
        arr.forEach((item) => {
          item.isChecked = 0;
        });
        //点击的那个售卖属性值变为1
        saleAttrValue.isChecked = 1;
      },
      //表单元素修改产品个数
      changeSkuNum(event) {
        //用户输入进来的文本 * 1
        let value = event.target.value * 1;
        //如果用户输入进来的非法,出现NaN或者小于1
        if (isNaN(value) || value < 1) {
          this.skuNum = 1;
        } else {
          //正常大于1【大于1整数不能出现小数】
          this.skuNum = parseInt(value);
        }
      },
      dePrice(){
        alert("产品暂时还未降价哦！");
      },
      //加入购物车  async函数的执行，返回值一定是promise （成功或者是失败）
      async addShopcar() {
        //1:在点击加入购物车这个按钮的时候，做的第一件事情，将参数带给服务器（发请求），通知服务器加入购车的产品是谁
        //this.$store.dispatch('addOrUpdateShopCart'),说白了，它是在调用vuex仓库中的这个addOrUpdateShopCart函数。
        //2:你需要知道这次请求成功还是失败，如果成功进行路由跳转，如果失败，需要给用户提示
        try {
          //成功
          await this.$store.dispatch("addOrUpdateShopCart", {
            skuId: this.$route.params.skuid,
            skuNum: this.skuNum,
          });
          //3:进行路由跳转
          //4:在路由跳转的时候还需要将产品的信息带给下一级的路由组件
          //一些简单的数据skuNum，通过query形式给路由组件传递过去
          //产品信息的数据【比较复杂:skuInfo】,通过会话存储（不持久化,会话结束数据在消失）
          //本地存储|会话存储，一般存储的是字符串
          sessionStorage.setItem("SKUINFO",JSON.stringify(this.skuInfo));
          this.$router.push({name:'addcartsuccess',query:{skuNum:this.skuNum}});
        } catch (error) {
          //失败
          alert(error.message);
        }
      },
    }
  }
</script>

